

import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import { find, map, filter } from 'lodash'

import { BUILDINGS } from 'data'

import $ from 'jquery'

import BScroll from 'better-scroll'
import api from '../service/api'

let tabs = [
{name: '置顶', en: 'top'},
{name: '热帖', en: 'hot'},
{name: '精华', en: 'Essence'},
{name: '业主加群比较长比较长比较长', en: 'long'},
{name: '深井冰', en: 'sjb'},
{name: '求偶', en: 'qo'}
]

const menus = {
menu1: '北京烤鸭',
menu2: '陕西油泼面',
menu3: '西安肉夹馍'
}

let topics = [
{title: '水管漏水了', content: '水管漏水了2333333'},
{title: '厕所堵了', content: '水管漏水了2333333'},
{title: '隔壁太大声了', content: '水管漏水了2333333'},
{title: '水管漏水了', content: '水管漏水了2333333'},
{title: '厕所堵了', content: '水管漏水了2333333'},
{title: '隔壁太大声了', content: '水管漏水了2333333'},
{title: '水管漏水了', content: '水管漏水了2333333'},
{title: '厕所堵了', content: '水管漏水了2333333'},
{title: '隔壁太大声了', content: '水管漏水了2333333'},
{title: '水管漏水了', content: '水管漏水了2333333'},





{title: '水管漏水了', content: '水管漏水了2333333'}
]

export default {
props: {

},
data () {
return {
beforePullDown: true,
bubbleY: 0,
isPullingDown: false,
isPullUpLoad: false,
topics: [],
tabs: tabs,
activeTab: tabs[0],
menus: menus,
showList: false,

bsConf: {
probeType: 3,
click: true,
tap: false,
listenScroll: true,
pullDownRefresh: true,
scrollbar: true,
pullUpLoad: {
threshold: 50
}
},
scrollWidth: '',
pullDownStyle: {
top: '-50px'
},
pullDownInitTop: -50,
activeNavIndex: 0,
titleBarStyle: {
width: '',
left: '',
right: ''
},
isTitleBarForward: true
}
},
computed: {
titleBarClass () {
return {
'backward': !this.isTitleBarForward,
'forward': this.isTitleBarForward
}
},
place () {
return map(filter(BUILDINGS, item => item.en === this.$route.params.name), 'name')[0]
}
},
watch: {
'$route.query' () {
this.activeTab = find(this.tabs, tab => tab.en === this.$route.query.tab)
this.fetchData()
}
},
created () {
this.fetchData()
},
mounted () {
this.$nextTick(() => {
this.setScrollWidth()

this.scroll = new BScroll(this.$refs.titles, {
scrollX: true,
scrollY: false,
tap: true
})
this.scroll.on('touchEnd', pos => {
console.log(pos)
})




let wrapperDom = document.querySelector('.scroll-titles')
let titlesDom = document.querySelectorAll('.title')
wrapperDom.addEventListener('tap', e => {
this.onNavTap(e)
}, false)

})
},
methods: {
demo () {

},
refresh () {

window.location.reload()
},
getMore () {

},
getSelectedMenu (key, item) {
this.$vux.toast.text(item, 'middle')

},
gotoHome () {
this.$router.push({name: 'Home'})
},
gotoTopic (e, topic) {
this.$router.push({name: 'Topic', query: {page: 1}})
},
onClick (e, item, idx) {
console.log(e)
},
onNavTap (e) {
let $el = $(e.target)
let idx = $(e.target).index()

this.$router.push({query: {tab: tabs[idx].en}})






},
fetchData () {
this.topics.splice(0)
api.getTopics()
.then(({ data }) => {
this.topics = data
})
.catch(err => {

})
},
setScrollWidth () {
let totalWidth = 0
this.$refs.lis.forEach(li => {
totalWidth += li.getBoundingClientRect().width
})
this.scrollWidth = `${totalWidth}px`
},
filter () {

},
onScroll (pos) {

},
onPullingDown () {
const me = this
this.beforePullDown = false
this.isPullingDown = true

setTimeout(() => {

if (Math.random() > -1) {

this.topics.unshift({title: `隔壁太大声了-${Date.now()}`, content: '水管漏水了'})
this.$nextTick(() => {
this.$refs.scroll.scroll.finishPullDown()
this.isPullingDown = false
setTimeout(() => {
this.pullDownStyle = `top:${this.pullDownInitTop}px`
this.beforePullDown = true
this.$refs.scroll.scroll.refresh()
}, this.$refs.scroll.scroll.options.bounceTime)
})
} else {


}
}, 2000)
},
onPullingDownScroll (pos) {
if (this.beforePullDown) {
this.bubbleY = Math.max(0, pos.y + this.pullDownInitTop)
this.pullDownStyle = Object.assign({}, this.pullDownStyle, {
top: Math.min(pos.y + this.pullDownInitTop, 10) + 'px'
})
} else {
this.bubbleY = 0
}
},
onPullingUp () {
this.isPullUpLoad = true
setTimeout(() => {
if (Math.random() > -1) {
this.topics.push({title: `隔壁太大声了-${Date.now()}`, content: '水管漏水了'})
this.$nextTick(() => {
this.isPullUpLoad = false
this.$refs.scroll.scroll.finishPullUp()

this.$refs.scroll.scroll.refresh()
})
}
}, 2000)
}
},
components: {
actionsheet: Actionsheet
}
}



