import { mapState, mapGetters } from 'vuex'
export default {
  name: 'app',
  computed: {
    ...mapState({
      iosTapBack: state => state.page.iosTapBack,
    }),
    ...mapGetters([
      'isForward'
    ]),
    animateType () {
      if (this.iosTapBack) {
        return ''
      }
      return this.isForward ? 'vux-pop-in' : 'vux-pop-out'
    }
  }
}



