import { BUILDINGS } from 'data'
import { mapMutations } from 'vuex'
export default {
  data () {
    return {
      city: '广州',
      buildings: BUILDINGS
    }
  },
  methods: {
    ...mapMutations([
      'RECORD_PAGE_SWITCH_ROUTE'
    ]),
    gotoPlace (item) {
      this.RECORD_PAGE_SWITCH_ROUTE({
        to: 'Place',
        from: 'Home'
      })
      this.$router.push({
        name: 'Place',
        params: {name: item.en}
      })
    }
  }
}



