


import Vue from 'vue'
import FastClick from 'fastclick'

import App from './App'

import { sync } from 'vuex-router-sync'

import { get } from 'lodash'

import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin, {position: 'top'})

import './globalComponents'
import '@/css/normalize.css'
import '@/css/public.css'
import '@/css/page.css'



Vue.prototype.$get = get

import router from './router/index'
import store from './store/index'

store.registerModule('vux', { // 名字自己定义
state: {
isLoading: false
},
mutations: {
updateLoadingStatus (state, payload) {
state.isLoading = payload.isLoading
}
}
})

FastClick.attach(document.body)

Vue.config.productionTip = false

sync(store, router)


let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']

document.addEventListener('touchend', () => {
endTime = Date.now()
})
methods.forEach(key => {
let method = router[key].bind(router)
router[key] = function (...args) {
isPush = true
method.apply(null, args)
}
})







import Page from './store/modules/page'
router.beforeEach((to, from, next) => {
console.log(Page.state.fromPage)

if (!isPush && (Date.now() - endTime) < 377) {
store.commit('IOS_TAP_BACK', true)
} else {
store.commit('IOS_TAP_BACK', false)
}
isPush = false

store.commit('RECORD_PAGE_SWITCH_ROUTE', { to, from })
store.commit('updateLoadingStatus', {isLoading: true})
next()
})

router.afterEach((to, from) => {




store.commit('updateLoadingStatus', {isLoading: false})
})








/* eslint-disable no-new */
new Vue({
store,
router,
render: h => h(App)
}).$mount('#app-box')


import { userAgent } from 'tool/userAgent'
console.log(userAgent)


